'use client';
import React, { useEffect, useState } from 'react';

type Session = {
  title: string;
  description: string;
  start_time: string;
  end_time: string;
  day: string;
};

type ScheduleData = {
  [day: string]: Session[];
};

const SchedulePage: React.FC = () => {
  const [schedule, setSchedule] = useState<ScheduleData>({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  useEffect(() => {
    fetch('http://127.0.0.1:8000/api/schedule/')
      .then((res) => {
        if (!res.ok) throw new Error('Fetch failed');
        return res.json();
      })
      .then((data: Session[]) => {
        const grouped: ScheduleData = {};
        data.forEach((item: Session) => {
          const dayLabel = item.day || 'General'; // ‚úÖ Use string label directly
          if (!grouped[dayLabel]) grouped[dayLabel] = [];
          grouped[dayLabel].push(item);
        });
        setSchedule(grouped);
        setLoading(false);
      })
      .catch(() => {
        setError(true);
        setLoading(false);
      });
  }, []);

  const formatTime = (iso: string) => {
    const date = new Date(iso);
    return date.toLocaleString('en-US', {
      weekday: 'short',
      month: 'short',
      day: 'numeric',
      hour: 'numeric',
      minute: '2-digit',
    });
  };

  if (loading) return <p className="text-center">Loading schedule...</p>;
  if (error) return <p className="text-center text-red-500">Failed to load schedule.</p>;

  return (
    <div className="max-w-4xl mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-6 text-center">üõ∞Ô∏è Event Schedule</h1>
      {Object.entries(schedule).map(([day, sessions]) => (
        <div key={day} className="mb-8">
          <h2 className="text-xl font-semibold mb-4 text-blue-700">üìÖ {day}</h2>
          <div className="grid gap-4">
            {sessions.map((session, idx) => (
              <div key={idx} className="border rounded-lg p-4 shadow-sm hover:shadow-md transition">
                <h3 className="text-lg font-bold">{session.title}</h3>
                <p className="text-gray-600">{session.description}</p>
                <p className="text-sm text-gray-500 mt-2">
                  {formatTime(session.start_time)} ‚Äì {formatTime(session.end_time)}
                </p>
              </div>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};

export default SchedulePage;
